// Animation

@keyframes float {
	0% {
		box-shadow: 0 5px 15px 0px rgba(#FFF,0.6);
		transform: translateY(0);
	}
	50% {
		box-shadow: 0 25px 15px 0px rgba(#FFF,0.2);
		transform: translateY(-5px);
	}
	100% {
		box-shadow: 0 5px 15px 0px rgba(#FFF,0.6);
		transform: translatey(0);
	}
}