.gallery {
  background-color: $white;
  grid-column: full-start / end-end;

  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(10, 6vw);
  grid-gap: 2px;
  padding: 2px;

  @media screen and (min-width: $breakpoint-sm) {
    grid-gap: .5rem;
    padding: .5rem;
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__item {

    &--1 {
      grid-row: 1 / span 2;
      grid-column: 1 / span 2;
    }

    &--2 {
      grid-row: 1 / span 3;
      grid-column: 3 / span 3;
    }

    &--3 {
      grid-row: 1 / span 2;
      grid-column: 6 / span 1;
    }

    &--4 {
      grid-row: 1 / span 2;
      grid-column: 7 / span 2;
    }

    &--5 {
      grid-row: 3 / span 3;
      grid-column: 1 / span 2;
    }

    &--6 {
      grid-row: 4 / span 2;
      grid-column: 3 / span 2;
    }

    &--7 {
      grid-row: 4 / span 1;
      grid-column: 5 / span 1;
    }

    &--8 {
      grid-row: 3 / span 2;
      grid-column: 6 / span 2;
    }

    &--9 {
      grid-row: 3 / span 3;
      grid-column: 8 / span 1;
    }

    &--10 {
      grid-row: 6 / span 2;
      grid-column: 1 / span 1;
    }

    &--11 {
      grid-row: 6 / span 2;
      grid-column: 2 / span 2;
    }

    &--12 {
      grid-row: 6 / span 2;
      grid-column: 4 / span 1;
    }

    &--13 {
      grid-row: 5 / span 3;
      grid-column: 5 / span 3;
    }

    &--14 {
      grid-row: 6 / span 3;
      grid-column: 8 / span 1;
    }

    &--15 {
      grid-row: 9 / span 2;
      grid-column: 7 / span 2;
    }

    &--16 {
      grid-row: 8 / span 3;
      grid-column: 1 / span 1;
    }

    &--17 {
      grid-row: 8 / span 2;
      grid-column: 2 / span 2;
    }

    &--18 {
      grid-row: 8 / span 1;
      grid-column: 6 / span 2;
    }

    &--19 {
      grid-row: 8 / span 3;
      grid-column: 4 / span 2;
    }

    &--20 {
      grid-row: 9 / span 2;
      grid-column: 6 / span 1;
    }

    &--21 {
      grid-row: 10 / span 1;
      grid-column: 2 / span 2;
    }
  }
}
