.counter {
  
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__number {
    font-size: 6rem;
    color: $orange-brand;
    font-weight: 600;
  }

  &__icon {
    fill: $yellow-brand;
    width: 8rem;
    height: 8rem;
  }
}